.pane {
    background-color: rgb(41, 41, 40);
    font-size: 4em;
    position: relative;
    flex-grow: 1;
    
    &.shrink {
        word-break: break-all;
    }
}

.message {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    transition: top 0.5s ease, left 0.5s ease 0.2s;

    .shrink & {
        top: 0;
        left: 0;
        transform: translate3d(0, 0, 0);
    }
}